<template>
  <div class="main-container">
    <div class="container-result">
      <span class="title result-title">Your Result</span>
      <div class="result">
        <div class="result-score">{{getAverage}}</div>
        <div class="result-score-title">of 100</div>
      </div>
      <div class="footer">
        <span class="title result-score-footer-title">{{getScoreTitle}}</span>
        <span class="title result-score-footer-frase">{{getScoreFrase}}</span>
      </div>
    </div>
    <div class="container-info">
        <span class="title">Summary</span>
        <div class="info-item" v-for="(item,index) in data" :key="index">
            <div class="info-item-intro">
              <img :src="`${item.icon}`" alt="icon" />
            <span class="info-item-title">{{item.category}}</span>
            </div>
            <span class="info-item-value">{{item.score}}<span style="font-family: 'hanken';font-weight: 400;color:#CCD1D1"> / 100</span></span>
        </div>
        <div class="continue-button">
          Continue
        </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    data: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
        icons:[],
        colors: ["#FADBD8", "#FEF9E7", "#E8F8F5", "#EAF2F8"],
        colorsTitle: ["#CB4335", "#F4D03F", "#1ABC9C", "#3498DB"],
        
    };
  },
  computed: {
    getAverage() {
      const sum = this.data.reduce((acc, item) => {
        return acc + item.score;
      }, 0);
      return Math.floor(sum / this.data.length);
    },
    getScoreTitle(){
      const avg=this.getAverage
      if(avg<50){
        return "Low"
      }else if(avg<70){
        return "Medium"
      }else{
        return "High"
      }
    },
    getScoreFrase(){
      let frase=""
      switch(this.getScoreTitle){
        case "Low":
          frase= "You need to improve your score to be better"
          break;
        case "Medium":
          frase= "You are doing well but you can do better"
          break;
        case "High":
          frase= "You are doing great! Keep it up"
          break;
      }
      return frase
    }
  },
  mounted() {
    this.setItemsColor();
  },
  methods: {
    setItemsColor() {
     const items = document.querySelectorAll(".info-item");
     const itemsTitle = document.querySelectorAll(".info-item-title");
      items.forEach((item, index) => {
        item.style.background = this.colors[index];
        itemsTitle[index].style.color = this.colorsTitle[index];
      });
    },
  },
};
</script>
<style>
