  background: linear-gradient(184deg, rgba(63,0,255,1) 0%, rgba(12,0,144,1) 100%);
  padding: 3em 2.0em 0 2em;
  row-gap: 1.4em;
}	.container-info{
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 100%;
